import React, { Component } from 'react';
import { Steps, Button } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { connect } from 'react-redux';
import { fromJS, is } from 'immutable';

class Xsteps extends Component {
	state = {
		active: 0
	}

	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}
	
	next = (n) => {
		let active = this.state.active + 1;
		if (active > 3) {
			active = 0;
		}
		this.setState({ active });
	}

	render() {
		const { defaultOptions} = this.props.configs

		return (
			<div id="x-time-picker">
				<h3>Steps 步骤条</h3>
				<div className="mt-20 mb-20">
					<Steps space={200} active={this.state.active} finishStatus="success">
						<Steps.Step title="步骤 1"></Steps.Step>
						<Steps.Step title="步骤 2"></Steps.Step>
						<Steps.Step title="步骤 3"></Steps.Step>
					</Steps>
					<Button onClick={this.next}>下一步</Button>
				</div>
				<Codemirror value={
					`<Steps space={200} active={this.state.active} finishStatus="success">
	<Steps.Step title="步骤 1"></Steps.Step>
	<Steps.Step title="步骤 2"></Steps.Step>
	<Steps.Step title="步骤 3"></Steps.Step>
</Steps>
<Button onClick={this.next}>下一步</Button>`
				} options={defaultOptions} />

			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

	})(Xsteps)
